body.illustration header {
  background-color: #eee;
}

body.home header {
  background-color: transparent;
  h1 {
    a {
      color: #fff;
    }
  }

  nav {
    a, span {
      color: #fff;
    }
  }
}

header {
  position: fixed;
  top: 0;
  padding: 30px 50px;
  width: 100%;
  height: 100px;
  z-index: 100;
  background-color: #fff;

  h1 {
    position: absolute;

    a {
      text-decoration: none;
      color: #353535;
      font-size: 22px;
      font-weight: 400;
      line-height: 40px;
    }
  }

  h2 {
    color: #353535;
    font-size: 15px;
    font-weight: 400;
    line-height: 40px;
    text-align: center;
  }

  nav {
    position: absolute;
    top: 0;
    right: 35px;

    ul li {
      display: inline-block;
      padding: 35px 15px;

      a,
      span {
        display: inline-block;
        color: #9c9c9c;
        font-size: 22px;
        line-height: 30px;
        text-decoration: none;
        cursor: pointer;
        -webkit-transition: all .5s;
        transition: all .5s;
      }

      a:hover,
      span:hover {
        color: #353535;
      }

      ul {
        display: none;
        padding-left: 10px;

        li {
          display: inline-block;
          padding-top: 0;
          padding-bottom: 0;
          color: #9c9c9c;
          font-size: 19px;
          cursor: pointer;

          &:last-of-type {
            padding-right: 0;
          }

          &.state {
            color: #353535;
          }
        }
      }
    }
  }
}

.page-template-page-about h2,
.page-template-page-home h2 {
  display: none;
}

.page-template-page-projects {
  h2 {
    display: none;
  }

  header {
    background-color: transparent;
  }
}

.page-template-page-about header nav ul li.state--about a,
.page-template-page-projects header nav ul li span {
  color: #353535;
}