body {
  color: #353535;
  background-color: #fff;
}

.med,
.small,
.tablet {
  display: none !important;
}

@media only screen and (max-width: 64em) {
  .tablet {
    display: block !important;
  }

  .desktop {
    display: none !important;
  }
}

@media only screen and (max-width: 48em) {
  .med {
    display: block !important;
  }

  .desktop,
  .large {
    display: none !important;
  }
}

@media only screen and (max-width: 33.75em) {
  .desktop {
    display: none !important;
  }

  .small {
    display: block !important;
  }
}

main {
  top: 0;
  position: absolute;
  width: 100%;
  height: 100%;

  .cover {
    position: absolute;
    width: 100%;
    height: 100%;
    background-image: url(img/test.png);
    background-size: cover;
    background-position: 50%;
  }

  .footnote {
    position: absolute;
    bottom: 20px;
    left: 30px;
    color: #000;
    font-size: 12px;
  }
}

.boxes {
  position: absolute;
  top: 80px;
  left: 0;
  margin-bottom: 30px;
  margin-left: 50px;
  margin-right: 50px;
  width: calc(100% - 100px);
  -webkit-column-count: 4;
  -moz-column-count: 4;
  column-count: 4;
  -webkit-column-gap: 40px;
  -moz-column-gap: 40px;
  column-gap: 40px;
  -webkit-column-fill: initial;
  -moz-column-fill: initial;
  column-fill: initial;

  .box {
    padding-top: 20px;
    padding-bottom: 20px;
    -webkit-column-break-inside: avoid;

    &:hover img {
      -webkit-filter: blur(5px);
      filter: blur(5px);
    }

    img {
      width: 100%;
      height: auto;
      transition: all .25s;
    }
  }
}

@media screen and (max-width: 1680px) {
  .boxes {
    -webkit-column-count: 3;
    -moz-column-count: 3;
    column-count: 3;
  }
}

@media screen and (max-width: 1260px) {
  .boxes {
    -webkit-column-count: 2;
    -moz-column-count: 2;
    column-count: 2;
  }
}

@media screen and (max-width: 840px) {
  .boxes {
    -webkit-column-count: 1;
    -moz-column-count: 1;
    column-count: 1;
  }
}

body,
html {
  position: relative;
  height: 100%;
  font-size: 0;
}

body {
  &.illustration {
    background-color: #eee;

    .project .article--container {
      background-color: #eee;
    }
  }

  &.single {
    header {
      border-bottom: 0;

      h1,
      nav {
        display: none;
      }
    }

    .project--tool {
      .read {
        position: fixed;
        top: 30px;
        left: 50px;
        color: #9c9c9c;
        font-size: 15px;
        line-height: 40px;
        text-decoration: none;
        cursor: pointer;
        z-index: 200;
        transition: all .5s;

        &:hover {
          color: #353535;
        }
      }

      .close {
        position: fixed;
        top: 30px;
        right: 50px;
        z-index: 200;

        a {
          color: #9c9c9c;
          font-size: 15px;
          line-height: 40px;
          text-decoration: none;
          transition: all .5s;

          &:hover {
            color: #353535;
          }
        }
      }

      .pagination {
        display: none;
        position: fixed;
        bottom: 10px;
        left: 30px;
        z-index: 200;
        font-size: 15px;
        line-height: 40px;
      }

      .next-project {
        position: fixed;
        bottom: 30px;
        left: 0;
        z-index: 200;
        width: 100%;
        text-align: center;

        a {
          color: #9c9c9c;
          font-size: 15px;
          line-height: 40px;
          text-decoration: none;
          transition: all .5s;

          &:hover {
            color: #353535;
          }
        }
      }
    }
  }
}

.project-name {
  position: absolute;
  top: 30px;
  left: 30px;
  font-size: 15px;
}

.project {
  padding-top: 100px;
  width: 100%;
  height: calc(100% - 200px);
  font-size: 0;

  .article--container {
    display: table;
    position: absolute;
    opacity: 0;
    top: 100px;
    left: 0;
    z-index: 1000;
    width: 100%;
    height: calc(100% - 100px);
    background-color: #fff;
    transition: all .5s ease-in-out;
    pointer-events: none;

    .article {
      display: table-cell;
      vertical-align: middle;
      padding-left: 25%;
      padding-right: 25%;
      width: 50%;
      font-size: 32px;
      line-height: 1.5;
      text-align: center;
      em {
        font-style: italic;
      }
    }
  }

  .next {
    position: fixed;
    top: 100px;
    z-index: 500;
    width: 50%;
    height: calc(100% - 200px);
  }

  .prev {
    position: fixed;
    top: 100px;
    z-index: 500;
    width: 50%;
    height: calc(100% - 200px);
    left: 0;
    cursor: url(img/prev@2x.png), pointer;
  }

  .next {
    right: 0;
    cursor: url(img/next@2x.png), pointer;
  }

  .swiper-wrapper {
    transition: ease;
  }

  .swiper-container {
    width: 100%;
    height: 100%;
  }

  .swiper-slide {
    display: block;
    text-align: center;

    img {
      max-width: 100%;
      max-height: 100%;
      width: auto;
      height: auto;
    }
  }
}

.main--about {
  position: static;
  padding-top: 150px;
  font-size: 10px;

  .headline {
    padding-bottom: 60px;

    p {
      margin-bottom: 25px;
      font-size: 22px;
      line-height: 1.5;
    }
  }

  .clients p,
  .contact p,
  .credit p {
    margin-bottom: 15px;
    font-size: 17px;
    line-height: 1.5;
  }

  .contact,
  .credit {
    display: inline-block;
    vertical-align: top;
    margin-bottom: 30px;
  }

  .contact a,
  .credit a {
    color: #353535;
    text-decoration: none;
  }

  .contact {
    width: 45%;
  }

  .credit {
    width: 55%;
  }

  .clients {
    padding-bottom: 90px;
  }

  .copyright {
    padding-bottom: 30px;
    font-size: 15px;
  }
}